import logo from './Img/logo.png';

import { Link } from 'react-router-dom';
const Navbar = () => {
    return(
        <nav className="navbar">
            <img src={logo} alt='vec logo' width='100px' height='100px'></img><h1>College Bus</h1>
            <div className="links">
                <Link to="/"><i className='fa fa-home'></i>Home</Link>
                <Link to="/Locate"><i className="fa fa-map-marker"></i>Locate</Link>
                <Link to="/Registration"><i className='fa fa-user'></i>Registration</Link>
            </div>
        </nav>
    );
}

export default Navbar;