import React, { useState } from "react";

const Notification = (props) => {
    const [exit, setExit] = useState(false);
    const [intervalID, setintervalId] = useState(null);
    const [width, setWidth] = useState(0);

    const handledStartTimer = () => {
        const id = setInterval(() => {
            setWidth((prev) => {
                if(prev < 100){
                    return prev + 0.5;
                }
                clearInterval(id);
                return prev;
            })
        }, 20);

        setintervalId(id);
    };

    const handledCloseNotification = () => {
        handledPausedTimer();
        setExit(true);
        setTimeout(() => {
            props.dispatch({
                type: "REMOVE_NOTIFICATION",
                id: props.id
            })
        }, 400)
    };

    React.useEffect(() => {
        if(width === 100){
            handledCloseNotification()
        }
    });
    
    const handledPausedTimer = () => {
        clearInterval(intervalID)
    };

    React.useEffect(() => {
        handledStartTimer()
    }, [])

    return(
        <div onMouseEnter={handledPausedTimer} onMouseLeave={handledStartTimer} className={`notification-item ${props.type === "Success" ? "success" : "error"} ${exit ? "exit" : ""}`}>
            <p className="message">{props.Message}</p>
            <div className="bar" style={{width: `${width}%`}}/>
        </div>
    )
};
export default Notification;