import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import {React, useCallback } from 'react';
import { useState } from 'react';
import database from './FirebaseConnectivity';
import {getDatabase,onValue,ref,child} from 'firebase/database';
import { useLocation } from 'react-router-dom';

const containerStyle = {
    width: "100%",
    height: "500px",
};

const Map = (props) => {
    const location = useLocation()
    const {Bus} = location.state
    var db_latitude,db_longitude,s,s1,s2,r,r2,r3;
    var db = getDatabase(database);
    var database_longitude = getDatabase(database);
    s = ref(db,'Locations');
    s1 = child(s,"Bus No "+ Bus);
    s2 = child(s1, "latitude");
    onValue(s2, (snapshot => {
        db_latitude =  snapshot.val();
    }));
    r = ref(database_longitude, 'Locations');
    r2 = child(r, 'Bus No '+ Bus);
    r3 = child(r2, 'longitude');
    onValue(r3, (snapshot => {
        db_longitude = snapshot.val();
    }));
    const[map, setMap] = useState(null);
    const[currentLoc, setCurrentLoc] = useState({
    lat: 13.1497762,
    lng: 80.1912997,
    });
    
    const onLoad = useCallback(function callback(map){
        setMap(map);
        setInterval((props) => {
            setCurrentLoc({
                lat: db_latitude,
                lng: db_longitude,
            });
        },5000);
    },[db_latitude,db_longitude]);
    return(<>
    <div className='inputB'>
        <input className='input1' type='number'/>
    </div>
    <LoadScript googleMapsApiKey="AIzaSyD1pmWA-NIDna3ggbAKNDiDZNM7gAuiwI8">
        <GoogleMap
            mapContainerStyle={containerStyle}
            center={{lat: currentLoc.lat, lng: currentLoc.lng}}
            zoom={15}
            onLoad={onLoad}
        >{props.Busno}
            <Marker 
                position={{lat: currentLoc.lat,
                    lng: currentLoc.lng}}
            > 
            </Marker>
        </GoogleMap>
    </LoadScript>
    </>);
}


export default Map;