import logo from './Img/logo2.png';
import { useState } from 'react';
import {BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom';
import Map from './Map';

const link = "https://github.com/sekarmoorthy/App-development/raw/main/collegebustracking.apk";
const img = logo;
function Home() {
    const [Bus_no, setBus] = useState(4);
        return(
            <main className="contentinhome">
                <div className="main-content">
                    <h1>College Bus Tracking</h1>
                    <p className='para'>This Webpage used to track the college bus in real time enabling  the parents/student to use the college transport with ease and helping the transport coordinates with mobile Tracking System and Registration for the student without downloading the app. </p>
                    <br></br>
                    <p className='para1'>Enter your Provided Bus Number to track your Bus<br></br><input type='text' onChange={(e) => setBus(e.target.value)}></input>&nbsp; 
                    <Link to={{pathname: "/Locate", state: {
                        Bus : Bus_no
                    }}} className='hov' >Locate !&nbsp;<i className='fa fa-map-marker'></i></Link></p>
                    <br></br>
                    <Router>
                        <Switch>
                            <Route exact path='/Locate'>
                                <Map val={Bus_no} /> 
                            </Route>
                        </Switch>
                    </Router>
                    <p className='para2'>To download the app <a className='hov' href={link}>Click Here!</a>&nbsp;<i className='fa fa-download'></i></p>
                </div>
                <img src={img} alt="alternate locater" className='img'></img>
            </main>
        );
}

export default Home;